/*
 *
 * SystemsPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { Route, withRouter, Link } from 'react-router-dom';
import { FormattedMessage, injectIntl } from 'react-intl';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import { Row, Col, Container, Button } from 'reactstrap';
import injectReducer from 'utils/injectReducer';
import makeSelectSystemsPage from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import messages from './messages';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import consoleMessages from 'containers/Console/messages';
import { BaseSystemModel } from 'cb-utils/console-entity-models';
import {
  systemSelectors,
  systemActions,
} from 'containers/Console/ducks/systems';
import SystemCard from 'containers/SystemCard';
import NewEntityModal from '../NewEntityModal';
import { Formik, Field, FormikProps } from 'formik';
import FormikInputWrapper from 'components/FormikInputWrapper';
import Yup from 'yup';
import { ImageFile } from 'react-dropzone';
import { isSubmitDisabled } from 'components/CbButton';
import { IpmAssetTypes } from 'cb-utils/console-entity-models/ipm';
import {
  createCreateOrInstallFromGitHubRequest,
  createCreateSystemFromFileRequest,
} from '../Console/actions/ipm';
import { PlusIcon } from 'components/Icons';
import { SystemIcon } from 'components/EntityIcons';
import LearnMoreLink from 'components/LearnMoreLink';
import { createResetAllModifiable } from 'containers/Console/actions/modifiableEnitites';
import { getPathToNewModalRoute } from 'cb-utils/getPathParams';
import { createStructuredSelector } from 'reselect';

export class SystemsPage extends React.PureComponent<
  StateToProps & IDispatchToProps & ReactIntl.InjectedIntlProps,
  {}
> {
  componentWillMount() {
    this.props.resetState();
    this.props.init();
  }

  render() {
    return (
      <React.Fragment>
        <Helmet title="Home" meta={[{ name: 'description', content: '' }]} />
        <Route
          path={getPathToNewModalRoute()}
          component={this.renderNewModal}
        />
        <ConsolePage
          subheader={this.renderSubHeader()}
          isFetchingAssets={this.props.isFetchingSystems}
          zeroState={{
            show: this.props.totalSystems === 0,
            title: consoleMessages.assetClassSystems,
            icon: <SystemIcon />,
            info: messages.zeroStateInfo,
            moreInfo: (
              <LearnMoreLink url="https://docs.clearblade.com/v/3/1-platform_concepts/System/" />
            ),
            addButton: this.renderAddButton({ size: 'lg' }),
          }}
        >
          <Container>
            <Row>
              {this.props.systems.map((s) => (
                <Col
                  // if appID isn't there we assume that the system was imported by cblib, which uses systemKey
                  key={s.appID || s.systemKey}
                  sm="6"
                  data-cy="system-card-container"
                >
                  <SystemCard system={s} />
                </Col>
              ))}
            </Row>
          </Container>
        </ConsolePage>
      </React.Fragment>
    );
  }

  renderNewModal = () => {
    const formatMessage = this.props.intl.formatMessage;
    return (
      <Formik
        validationSchema={Yup.object().shape({
          name: Yup.string()
            .required(formatMessage(consoleMessages.errorNameRequired))
            .matches(/^[^\s]+$/, formatMessage(consoleMessages.errorSpaces))
            .matches(
              /^[\w-]+$/,
              formatMessage(consoleMessages.errorSpecialCharacters),
            ),
        })}
        initialValues={
          {
            name: '',
            description: '',
          } as BaseSystemModel
        }
        onSubmit={(values: BaseSystemModel, actions) => {
          this.props.createEmptySystem(values);
        }}
        render={({
          values,
          touched,
          errors,
          dirty,
          submitForm,
        }: FormikProps<BaseSystemModel>) => {
          return (
            <NewEntityModal
              // @ts-ignore - Clark needs to fix this
              newEntityMsg={messages.newSystem}
              ipmContext={[IpmAssetTypes.SYSTEM]}
              submitForm={submitForm}
              fromScratchForm={
                <React.Fragment>
                  <Field
                    name="name"
                    label="Name"
                    data-cy="system-name"
                    component={FormikInputWrapper}
                  />
                  <Field
                    name="description"
                    label="Description"
                    component={FormikInputWrapper}
                  />
                </React.Fragment>
              }
              isFromScratchFormComplete={isSubmitDisabled({
                errors,
                touched,
              })}
              isSubmitting={this.props.SystemsPage.isSubmitting}
              createFromGitHub={this.props.createSystemFromGitHub}
              createFromFile={this.props.createSystemFromFile}
            />
          );
        }}
      />
    );
  };

  renderAddButton = ({ size = 'md' }) => (
    <Link to={getPathToNewModalRoute()}>
      <Button size={size} color="primary" data-cy="add-system">
        <PlusIcon />
        <FormattedMessage {...messages.addSystem} />
      </Button>
    </Link>
  );

  renderSubHeader = () => {
    return (
      <React.Fragment>
        <PageHeader
          header={<FormattedMessage {...consoleMessages.assetClassSystems} />}
          actionButton={this.renderAddButton}
        />
      </React.Fragment>
    );
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  SystemsPage: makeSelectSystemsPage(),
  systems: systemSelectors.entitiesArray,
  isFetchingSystems: systemSelectors.fetchAll.isLoading,
  totalSystems: systemSelectors.entitiesCount,
});

type IDispatchToProps = typeof mapDispatchToProps;

const mapDispatchToProps = {
  init: () => systemActions.fetchAll.request(),
  resetState: createResetAllModifiable,
  createEmptySystem: (info: BaseSystemModel) =>
    systemActions.create.request(info),
  createSystemFromGitHub: (id: string, githubUrl?: string) =>
    createCreateOrInstallFromGitHubRequest(id, undefined, githubUrl),
  createSystemFromFile: (file: ImageFile) =>
    createCreateSystemFromFileRequest(file),
};

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(
  withRouter,
  withReducer,
  withSaga,
  withConnect,
  injectIntl,
)(SystemsPage);



