/*
 *
 * SystemsPage reducer
 *
 */

import assign from 'immutable-assign';
import { ActionConstants as IpmConstants } from 'containers/Console/actions/ipmConstants';
import { ActionTypes as IpmActionTypes } from 'containers/Console/actions/ipm';
import { systemActions } from '../Console/ducks';

export interface State {
  isSubmitting: boolean;
}

export const initialState: State = {
  isSubmitting: false,
};

function systemsPageReducer(
  state: State = initialState,
  action: IpmActionTypes,
) {
  switch (action.type) {
    case systemActions.create.successType:
    case IpmConstants.CREATE_OR_INSTALL_FROM_GITHUB_SUCCESS:
    case IpmConstants.CREATE_SYSTEM_FROM_FILE_SUCCESS:
      return assign(state, (s) => s.isSubmitting, () => false);
    case IpmConstants.CREATE_OR_INSTALL_FROM_GITHUB_REQUEST:
    case IpmConstants.CREATE_SYSTEM_FROM_FILE_REQUEST:
      return assign(state, (s) => s.isSubmitting, () => true);
    case IpmConstants.CREATE_OR_INSTALL_FROM_GITHUB_ERROR:
      return assign(state, (s) => ({
        ...s,
        isSubmitting: false,
      }));
    default:
      return state;
  }
}

export default systemsPageReducer;



