import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the systemsPage state domain
 */
const selectSystemsPageDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by SystemsPage
 */

const makeSelectSystemsPage = () =>
  createSelector(selectSystemsPageDomain(), (substate) => substate);

export default makeSelectSystemsPage;
export { selectSystemsPageDomain };



