import { all, takeEvery, call, put } from 'redux-saga/effects';
import { SystemModel } from 'cb-utils/console-entity-models';
import { systemActions } from 'containers/Console/ducks/systems';
import { push } from 'react-router-redux';
import { detailUrls } from 'cb-utils/getUrls';
import { importSystemFromFile } from 'cb-utils/services/systems';
import {
  CreateSystemFromCbLibSuccess,
  CreateSystemFromFileRequest,
  createCreateSystemFromFileSuccess,
  createCreateSystemFromFileError,
} from '../Console/actions/ipm';
import { ActionConstants as IpmConstants } from 'containers/Console/actions/ipmConstants';

function* createFromFileSaga() {
  yield takeEvery(IpmConstants.CREATE_SYSTEM_FROM_FILE_REQUEST, function*(
    action: CreateSystemFromFileRequest,
  ) {
    try {
      const resp = yield call(importSystemFromFile, action.payload.file);
      // the response from import is missing `auth_service`, `reg_service`, `registration` and `token_tll`
      // and has the `appID` as `systemKey` and `appSecret` as `systemSecret`
      // so to have matching data in the store, fetch again
      yield put(systemActions.fetchAll.request());
      yield put(createCreateSystemFromFileSuccess(resp));
    } catch (e) {
      yield put(createCreateSystemFromFileError(e));
    }
  });
}

interface CreateEmptySystemSuccessType {
  type: string;
  payload: SystemModel;
}
function* createSystemSuccessSaga() {
  yield takeEvery(
    [
      IpmConstants.CREATE_OR_INSTALL_FROM_GITHUB_SUCCESS,
      IpmConstants.CREATE_SYSTEM_FROM_FILE_SUCCESS,
      systemActions.create.successType,
    ],
    function*(action) {
      try {
        let systemKey;
        if ((action as CreateEmptySystemSuccessType).payload.appID) {
          systemKey = (action as CreateEmptySystemSuccessType).payload.appID;
        } else if (
          (action as CreateSystemFromCbLibSuccess).payload.resp &&
          (action as CreateSystemFromCbLibSuccess).payload.resp.systemKey
        ) {
          systemKey = (action as CreateSystemFromCbLibSuccess).payload.resp
            .systemKey;
        }
        if (systemKey) {
          yield put(push(detailUrls.systems(systemKey)));
        }
      } catch (e) {
        return;
      }
    },
  );
}

// All sagas to be loaded
export default function*() {
  yield all([createSystemSuccessSaga(), createFromFileSaga()]);
}



